import { Inject, Injectable, LOCALE_ID } from '@angular/core';
import { DateFormatOptions, DateFormatNameOptions, NumberFormatOptions } from '@telerik/kendo-intl';
import { format as intlFormat, toString } from '@telerik/kendo-intl';
import { formatDate, parseDate, dateFormatNames, firstDay } from '@telerik/kendo-intl';
import { parseNumber, formatNumber } from '@telerik/kendo-intl';
import { IntlService } from './intl.service';

/**
 * The Internationalization service implemented by using
 * the CLDR Database via the @telerik/kendo-intl package.
 */
@Injectable()
export class CldrIntlService implements IntlService {
  /**
   * Creates a new instance of the service with the ID of the specified locale.
   *
   * Note that the parts of the locale ID can be separated by either `_` (underscore)
   * or `-` (dash).
   */
  constructor(@Inject(LOCALE_ID) public localeId: string) {
      // Angular locales use underscore, e.g. en_US
      // while IETF BCP-47 specifies a dash.
      // https://tools.ietf.org/html/bcp47
      this.localeId = this.localeId.replace(/_/g, '-');
  }

  /**
   * Formats a string with placeholders such as
   * `Total amount {0:c}`.
   */
  public format(format: string, ...values: any[]): string {
    return intlFormat(format, values, this.localeId);
  }

  /**
   * Converts an object to a string based on the specified format.
   */
  public toString(value: string, format: string): string {
    return toString(value, format, this.localeId);
  }

  /**
   * Converts a `Date` object to a string based on the specified format.
   * If no format is provided, the default short date format is used.
   */
  public formatDate(value: Date, format?: String|DateFormatOptions): string {
    return formatDate(value, format, this.localeId);
  }

  /**
   * Converts a string to a `Date` object based on the specified format.
   */
  public parseDate(value: string, format?: string|DateFormatOptions|string[]|DateFormatOptions[]): Date {
    return parseDate(value, format, this.localeId);
  }

  /**
   * Converts a string to a `Number`.
   */
  public parseNumber(value: string, format?: string|NumberFormatOptions): number {
    return parseNumber(value, this.localeId, format);
  }

  /**
   * Converts a `Number` to a string based on the specified format.
   */
  public formatNumber(value: number, format: string|NumberFormatOptions): string {
    return formatNumber(value, format, this.localeId);
  }

  /**
   * Returns the day names from the current locale based on the option.
   */
  public dateFormatNames(options: DateFormatNameOptions): any {
    return dateFormatNames(this.localeId, options);
  }

  /**
   * Returns the first day index starting from Sunday.
   */
  public firstDay(): number {
    return firstDay(this.localeId);
  }
}



